.drum-machine {
  display: flex;
  flex-direction: column;
  padding: 20px;
  gap: 10px;
}

.drum-machine__drum {
  height: 100px;
  width: 100px;
}

.drum-machine__drum_bd {
  background-color: blueviolet;
}

.drum-machine__drum_sd {
  background-color: brown;
}

.drum-machine__tempo-input {
  text-align: center;
}

.drum-machine__block {
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
}

.drum-machine__block-text {
  display: flex;
  width: 35px;
  height: 35px;
  align-items: center;
}

.drum-machine__step {
  display: flex;
  height: 35px;
  width: 35px;
  border: 1px solid grey;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
}

.drum-machine__step_chosen {
  background-color: coral;
}

.drum-machine__step_active {
  background-color: lightgreen;
}
